
 .intro{
     background-color: #f6feff;
     .comming-soon-lable {
        position: absolute;
        left: auto;
        right: -70px;
        top: 40px;
        background: $danger;
        color: $white;
        text-align: center;
        padding: 10px 15px;
        border-radius: 5px;
        transform: rotate(45deg);
        width: 260px;
    }
     .link-dasable{
         pointer-events: none;
     }
     #butter {
        z-index: -1;
    }
     #loading {
        background-color: $body-bg;
        height: 100%;
        width: 100%;
        position: fixed;
        margin-top: 0px;
        top: 0px;
        left: 0px;
        bottom: 0px;
        overflow: hidden !important;
        right: 0px;
        z-index: 999999;
    }
    #loading-center {
        background: url(../images/loader.gif) no-repeat scroll center center;
        background-size: 20%;
        width: 100%;
        height: 100%;
        position: relative;
    }
     h1,h2,h3,h4,h5,h6{
        margin-bottom: 0;
        color:#000;
        letter-spacing: -0.02em;
     }
     .font-weight-700{
         font-weight: 700;
     }
     .font-weight-400{
         font-weight: 400;
     }
 }

 .slick-slide{
    img {
        margin: 0 auto;
        box-shadow: 0 0px 30px -5px rgba(68 ,130 ,245 , 0.2);
        @if $enable-rounded == true {
            @include radius-mixin($border-radius);
        }
     }
     a{
        &:focus{
            outline:none!important;
        }
     }
 }

 .text-black{
     color:$black!important;
 }
.container-fluid {
    padding: 0 100px;
}
//  counter
#page-counter {
    padding: 30px 0;
}

.intro {
    section {
        position: relative;
        background: $white;
        padding: 80px 0 0;
        width: 100%;
        clear: both;
        z-index: 0;
        overflow: hidden;

        &.color-mode {
            height: 100%;
            width: 100%;
            padding-top: 100px;
            background: url(../images/intro/shape.png) no-repeat top;
            position: absolute;
            top: 0;
            z-index: -2;
        }
    }
    section.section-bg{
        background: $body-bg;
    }
    section.pack-builder{
        padding:100px 0 0;
    }
    .block-title-section{
        height: 700px;
    }
    .app-intro.lts{
        padding:100px 0!important;
    }
}

.pack-font{
    font-size: 2.5em;
    line-height: 1;
    position: relative;
    display: inline-block;
}
.small-text{
    font-size: 20px;
}

.purchase-btn-fixed {
    position: fixed;
    bottom: 40px;
    left: 40px;
    margin-bottom: 60px;
    height: 60px;
    background: $primary-light;
    @include shadow-mixin( 0 2px 4px rgba(0,0,0,.07));
    border-radius: 30px;
    color:$white;
    padding-right: 10px;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    z-index: 999;
    color:$white!important;
    &:before {
        content: "";
        position: absolute;
        z-index: 0;
        left: 50%;
        top: 50%;
        @include transform-mixin( translateX(-50%) translateY(-50%));
        display: block;
        width: 70px;
        height: 70px;
        background: $primary-light;
        @if $enable-rounded == true {
            @include radius-mixin($border-radius-circle);
        }
        animation: pulse-border 1500ms ease-out infinite;
    }
}
.p-icon{
    height: 40px;
    width: 40px;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    position: relative;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    background-color: $primary;
   @if $enable-rounded == true {
        @include radius-mixin($border-radius-circle);
    }
    margin: 10px 0 10px 10px;
    @include transition-mixin(all cubic-bezier(.4,0,.2,1) .4s);
}

.scroll-nav.n-fixed{
    position: fixed;
    top:0;
    left:0;
    right:0;
    width:100%;
    z-index:999!important;
    background-color: $primary;
	-webkit-animation-name: animationFade;
    -o-animation-name: animationFade;
    animation-name: animationFade;
    -webkit-animation-duration: 0.7s;
    -o-animation-duration: 0.7s;
    animation-duration: 0.7s;
    -webkit-animation-fill-mode: both;
    -o-animation-fill-mode: both;
    animation-fill-mode: both;
    border-radius:0;
    border-color:transparent;
    @include shadow-mixin(0px 0px 8px 0px rgba(0, 0, 0, 0.1));
}

.package-wrapper{
    .counter-title{
        font-weight: 700;
        font-size: 50px;
    }
    .count-text{
        font-size: 24px;
    }
    .figma-img{
        width: 45px;
        margin-bottom: 10px;
    }
}
.techno{
    .tech-box{
        h4{
            font-size: 26px;
            a{
                color:$white;
            }
        }
    }
    .right-sec{
        h2,p,span{
            color: $white;
        }
    }
}
#page-technology {
    padding: 55px 0;
}
.techno-color {
    padding: 100px 0;
    
    &::before {
        content: "";
        height: 125px;
        width: 100%;
        background: url(../images/intro/technology-bg.png) no-repeat top;
        position: absolute;
        bottom: 0;
        z-index: -1;
        background-size: cover;
        opacity: 0.2;
    }
}
.section-video {
    > iframe {
        background: white;
        padding: 5px;
        box-shadow: 0 10px 30px rgba(68,130,245,.2);
        border-radius: 5px;
    }
}
.ly-box{
    margin-bottom:50px;
     .card{
        margin-bottom: 25px;
    }
    .ly-title {
        h4 {
            a {
                color:$black;
                font-weight: 600;
            }
        }
    }
}

// product box
.title-wrapper{
    margin-bottom: 40px;
    position: relative;
}
.section-title{
    margin-bottom: 50px;
    .bFont{
        font-size: 95px;
    }
    .mFont{
        font-weight: 700;
        color:$black;
        font-size: 40px;
    }

}

.bg-icon-2{
    position: absolute;
    right: -58px;
    top: 50%;
    @include transform-mixin( translateY(-50%));
    width: 150px;
}
.bg-icon{
    position: absolute;
    right: -50px;
    top: 43%;
}
.bg-icon3{
    position: absolute;
    right: -50px;
    top: 39%;
}

//App Intro
.app-intro{
    padding:20px 0 0!important;
}
.app-intro-img{
    position: absolute;
    right: -82px;
    top: -20px;
    @include opacity-mixin(.1);
    -webkit-animation: bounces 2s infinite alternate;
    animation: bounces 2s infinite alternate;
}
.land-intro-img{
    position: absolute;
    left: -82px;
    top: -20px;
    @include opacity-mixin(.1);
    -webkit-animation: bounces 2s infinite alternate;
    animation: bounces 2s infinite alternate;
}
#app{
    .card {
         position: relative;
        width: 100%;
        padding: 5px;
        overflow: hidden;
        @include shadow-mixin(0 10px 30px rgba(68 ,130 ,245 , 0.2));
        &:hover{
            .box-title {
                bottom: 0;
                @if $enable-transitions == true {
                    @include transition-mixin(all 0.3s ease-in-out);
                }
            }
        }
    }
    .box-title {
        position: absolute;
        left: 0;
        right: 0;
        bottom: -60px;
        color: $white !important;
        margin: 0;
        display: block;
        padding: 15px;
        @if $enable-transitions == true {
            @include transition-mixin(all 0.3s ease-in-out);
        }
    }
}


//Package Builder
.white-sec{
    background:$white;
    height:400px;
    z-index:0;
    .cir-full {
        left: 50%;
        top: 23%;
        @include transform-mixin( translateX(-50%));
    }
}

.screens-img {
    img{
        border:3px solid $black;
        @if $enable-rounded == true {
            @include radius-mixin($border-radius);
        }
        height:265px;
    }
}

//Inner Pages

.inner-box{
    padding:15px;
}


// Menu style
.section-mountain {
    height: 400px;
    position: relative;
    overflow: hidden;
    margin-top: -200px;
    z-index: -1;
    &:before{
        position: absolute;
        content: "";
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
		background: url(../images/intro/slider/blur-img.png)no-repeat center top;
        background-size: cover;

    }
    &::after{
        position: absolute;
        content: "";
        top: 20%;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
		background: url(../images/intro/slider/mountain.png)no-repeat center bottom;
        background-size: cover;
    }
}

// core-feature
.featured-items {
    margin-top: 100px;
    .feature-content {
        margin: 0 auto;
        text-align: center;
        @if $enable-shadows == true {
            @include shadow-mixin($dropdown-box-shadow);
        }
        @if $enable-rounded == true {
            @include radius-mixin($border-radius);
        }
    }
    .figma-img {
        width: 80px;
    }
    .figma-img.s-icon{
        width: 23px;
    }
    .feature-icon {
        display: inline-block;
        height: 80px;
        width: 80px;
        line-height: 75px;
        text-align: center;
        background: $white;
    }
    .feature-detail{
        margin-top: 20px;
    }

}

// elements

// LTS
.lts-section {
    h1.lts-font {
        font-size: 200px;
        line-height: 160px;
        margin: 30px 0;
    }
    .bFont {
        font-size: 60px;
        margin-bottom: 20px;
    }
    h5{
        font-size: 40px;
    }
}

//Charts
.sec-higlight-box{
    position: absolute;
    left:50%;
    top:50%;
    @include transform-mixin( translate(-50%,-50%));
    display: inline-block;
    background: rgba(71,136,255,0.8);
    background: $primary;
    padding:60px;
    color:$white;
    @if $enable-rounded == true {
        @include radius-mixin($border-radius);
    }
}
.all-charts{
    overflow: hidden;
}
.charts-icons {
    display: flex;
    flex-wrap: wrap;
}
.chart-box {
    width: 12.5%;
    text-align:center;
    img {
        @include shadow-mixin(0 16px 40px 0 rgba(0,0,0,.0392157));
    }
}
.chart-box:nth-child(2n+0){
    margin-top: 70px;
}

#back-to-top{
    .top {
        z-index: 999;
        position: fixed;
        margin: 0px;
        color: $white;
        background: $primary;
        position: fixed;
        bottom: 30px;
        right: 40px;
        z-index: 999;
        font-size: 20px;
        width: 45px;
        height: 45px;
        text-align: center;
        line-height: 45px;
        @if $enable-rounded == true {
            @include radius-mixin($border-radius-circle);
        }
        @if $enable-transitions == true {
            @include transition-mixin(all 0.3s ease-in-out);
        }
        &:hover{
            background: $white;
            color: $primary;
        }
    }
}

.col-lg-5-c{
    text-align: center;
}

@-moz-keyframes spin { 100% { -moz-transform: rotate(360deg); } }
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

@media(max-width:1500px){
    .charts-icons{
        justify-content: center;
    }
}

@media(min-width:1300px) and (max-width:1500px){
    .chart-box{
        width: 15.5%;
    }
    .center-border.left {
            &:before {
                left: 0px !important;
                right: auto;
            }
        }
    
}

@media(max-width:1299px){
    .chart-box{
        width: 21.5%;
    }
    .center-border.left {
            &:before {
                left: 0px !important;
                right: auto;
            }
        }
}
@media(max-width:1199px) and (min-width:992px){
    .screens-img{
        img{
            height:192px;
        }
    }
    
}
@media(min-width:1024px){
    .section-title{
        .mFont{
            width:61%;
        }
    }
}

@media(min-width:992px){
    .col-lg-5-c{
        max-width: 20%;
        flex: 0 0 20%;
    }
    .center-border{
        &:before{
            content: '';
            position: absolute;
            right:-11px;
            top:50%;
            @include transform-mixin( translateY(-50%));
            background: $white;
            width: 2px;
            height:100%;
        }
    }
    .center-border.left{
        &:before{
            left:-30px;
            right:auto;
        }
    }
    .package-wrapper{
        .packge-content{
            padding-left:3.1rem;
        }
    }
    .techno{
        .packge-content{
