    }
    .right-sec{
        padding-top: 60px;
    }
    .intro {
        section {
            margin: 80px 0;
        }
    }
    .main-slider{
        margin-bottom: 0px;
    }
    .intro {
		section.color-mode {
			padding-top: 50px;
		}
	}
}

@media(max-width:767px){
    .header-wrapper{
        padding: 20px 15px !important;
        >.mr-3{
            margin-right: 0 !important;
        }
    }
    .slider-text{
        p.slider-sub-text{
            display: none;
        }
    }
    
    .screens-img{
        display: block!important;
        img{
        margin:15px 0;
        display: block;
        margin-left:auto;
        margin-right: auto;
        }
    }
    .app-intro {
        padding: 100px 0!important;
    }
    .footer-main-logo{
        img{
            width: 353px;
        }
    }
}

@media(max-width:675px){
    .chart-box{
        width: 25.5%;
        margin-bottom: 15px;
    }
    .chart-box:nth-child(2n+0) {
        margin-top: 0;
    }
    .slider-text {
		padding-top: 40px;
	}
    .scroll-nav{
        flex-wrap: nowrap;
        overflow-y: hidden;
        overflow-x: auto;
