    .header-wrapper{
        flex-direction: column;
    }
    .header-logo{
        margin-bottom: 18px;
    }
    .purchase-btn {
        >span{
            padding-right: 5px;
            margin-right: 5px;
        }
        >img{
            height: 15px !important;
            width: 15px !important;
        }
    }
}
@media(max-width:480px){
    .chart-box{
        width: 40%;
    }
    .slider-main-logo{
        img {
            width: 335px;
        }
    }
    .lts-section{
        h1.lts-font{
            font-size:150px;
        }
    }
    .bg-common:before{
        content:none;
    }
    .bg-common:after{
        content:none;
    }
    .footer-main-logo{
        img{
            width: 200px;
        }
    }
    .footer-text{
        h1{
            font-size: 2.3em;
        }
    }
    .slider-copyright{
        left: -55px;
    }
    .slider-breed{
        right: -70px;
    }
    .pack-font {
        font-size: 1.5em;
    }
    .section-title {
        .bFont {
            font-size: 75px;
        }
    }
    .voice-btn-fixed{
        right: 15px;
    }
    #back-to-top{
        .top{
            right: 15px;
        }
    }
    .purchase-btn-fixed{
        left: 15px;
    }

}
@media(max-width:360px){
    .chart-box{
        width: 100%;
    }  
}


@keyframes pulse-border {
	0% { @include transform-mixin( translateX(-50%) translateY(-50%) translateZ(0) scale(1)); @include opacity-mixin(1); }
	100% { @include transform-mixin( translateX(-50%) translateY(-50%) translateZ(0) scale(1.5));  @include opacity-mixin(0); }
}
@keyframes bounces {
	from { transform: translateY(0px); }
	to { transform: translateY(-25px); }
}
@-webkit-keyframes bounces {
	from { transform: translateY(0px); }
	to { transform: translateY(-25px); }
}

@keyframes side {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 100% 0;
  }
}
